import React from 'react';
import {
    Panel,
    PanelHeader,
    Placeholder,
    Cell,
    Group,
    List,
    Div,
    Button,
    FormLayout,
    Select,
    Input,
    Separator,
    Link
} from '@vkontakte/vkui';

import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import Icon28InfoOutline from '@vkontakte/icons/dist/28/info_outline';

import tovar1 from '../img/tovar1.jpg';
import tovar6 from '../img/tovar6.jpg';

class Basket extends React.Component {
    constructor (props) {
        super(props);

        this.state = {};
    }

render () {
    const {
        id,
        go,
    } = this.props;

return (
    <Panel id={id} separator={true}>
        <PanelHeader separator={true}>Корзина</PanelHeader>
            <Group>
                <Cell before={<Icon28InfoOutline />} multiline>
                    В Вашей корзине 2 товара на сумму 4250 баллов!
                </Cell>
            </Group>
            <Group>
                <List>
                    <Cell
                        onClick={go} data-to="testTovar"
                        before={
                            <div className="ico-align">
                                <img
                                    className="ico-crop"
                                    type="image"
                                    src={tovar1}
                                />
                            </div>
                        }
                        multiline
                    >
                        <span>MacBook Air</span>
                        <span className="disable-selectionn text">
                            Цена: 
                            4100 баллов
                        </span>
                        <span className="disable-selectionn text">
                            Категория:
                            Девайсы
                        </span>
                    </Cell>

                    <Cell
                        onClick={go} data-to="testTovar2"
                        before={
                            <div className="ico-align">
                                <img
                                    className="ico-crop"
                                    type="image"
                                    src={tovar6}
                                />
                            </div>
                        }
                        multiline
                    >
                        <span>Футболка</span>
                        <span className="disable-selectionn text">
                            Цена: 
                            150 баллов
                        </span>
                        <span className="disable-selectionn text">
                            Категория:
                            ÖMANKÖ for VK Testers
                        </span>
                    </Cell>
                </List>
            </Group>
            <Separator style={{paddingTop: 15}}/>
            <FormLayout>
                <Select top="Способ доставки" placeholder="Выберите способ доставки">
                    <option value="XS">Самовывоз из Зингера</option>
                    <option value="S">Доставка (минимальная сумма заказа — 200 баллов)</option>
                </Select>
                <Input type="text" top="Адрес" bottom="Почтовый адрес с индексом" />
                <Input type="text" top="Получатель" bottom="ФИО для отправки службой доставки" />
                <Input type="tel" top="Контактный телефон" />
                <Input type="text" top="Уточните детали заказа" />
            </FormLayout>
            <Div>
                <Button size="xl">Оформить заказ</Button>
            </Div>
            <Div className="clear-basket">
                <Link style={{color: 'red'}}>Очистить корзину</Link>
            </Div>
            {/*<Placeholder
                icon={<Icon28MarketOutline width={56} height={56} />}
                stretched
            >
                Ваша корзина пуста.<br></br>
                Перейдите в магазин, чтобы добавить товары в корзину.
            </Placeholder>*/}
        </Panel>
        );
    }
}

export default Basket;