import React from 'react';
import {
    Panel,
    PanelHeader,
    IOS,
    platform,
    PanelHeaderButton,
    Div,
    Button
} from '@vkontakte/vkui';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28MarketAddBadgeOutline from '@vkontakte/icons/dist/28/market_add_badge_outline';

import tovar1 from '../img/tovar1.jpg';

const osname = platform();

class TestTovar1 extends React.Component {
    constructor (props) {
        super(props);

        this.state = {};
    }

render () {
    const {
        id,
        go,
    } = this.props;

return (
    <Panel id={id} separator={false}>
        <PanelHeader
            left={
                <PanelHeaderButton onClick={go} data-to="store">
                    {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
                </PanelHeaderButton>
            }
        >
            MacBook Air
        </PanelHeader>
        <img className="open-tovar" type="image" src={tovar1}/>
            <Div>
                <div className="text-open-tovar zapret-videleniya">
                    <h1>MacBook Air</h1>
                </div>
                <div className="money-open-tovar zapret-videleniya">
                    <h3>4100 баллов</h3>
                </div>
                <div className="category-open-tovar zapret-videleniya">
                    Девайсы
                </div>
                <Div className="description-open-tovar zapret-videleniya">Space Gray, 13-inch, 256 GB, Touch ID</Div>
            </Div>
            <Div><Button size="xl" before={<Icon28MarketAddBadgeOutline />}>Добавить в корзину</Button></Div>
        </Panel>
        );
    }
}

export default TestTovar1;