import React from 'react';
import {
    Panel,
    PanelHeader,
    Tabs,
    TabsItem,
    HorizontalScroll,
    Separator,
    Counter,
    Placeholder
} from '@vkontakte/vkui';

import Icon28GhostOutline from '@vkontakte/icons/dist/28/ghost_outline';

class Orders extends React.Component {
    constructor (props) {
        super(props);

        this.state = {
            activeTab: 'active'
        };
    }

render () {
    const {
        id,
        go,
    } = this.props;

return (
    <Panel id={id} separator={false}>
        <PanelHeader separator={false}>Мои заказы</PanelHeader>
            <Tabs>
                <HorizontalScroll>
                    <TabsItem
                        after={<Counter size="s">1</Counter>}
                        onClick={() => this.setState({ activeTab: 'active' })}
                        selected={this.state.activeTab === 'active'}
                    >
                        Активные
                    </TabsItem>
                    <TabsItem
                        after={<Counter size="s">2</Counter>}
                        onClick={() => this.setState({ activeTab: 'completed' })}
                        selected={this.state.activeTab === 'completed'}
                    >
                        Выполненные
                    </TabsItem>
                    <TabsItem
                        after={<Counter size="s">3</Counter>}
                        onClick={() => this.setState({ activeTab: 'canceled' })}
                        selected={this.state.activeTab === 'canceled'}
                    >
                        Отмененные
                    </TabsItem>
                </HorizontalScroll>
            </Tabs>
            <Separator />
            {this.state.activeTab === "active" ? <div>
                <Placeholder
                    icon={<Icon28GhostOutline width={56} height={56} />}
                >
                    Активных заказов нет.
                </Placeholder>
            </div> : null}
            {this.state.activeTab === "completed" ? <div>
                <Placeholder
                    icon={<Icon28GhostOutline width={56} height={56} />}
                >
                    Выполненных заказов нет.
                </Placeholder>
            </div> : null}
            {this.state.activeTab === "canceled" ? <div>
                <Placeholder
                    icon={<Icon28GhostOutline width={56} height={56} />}
                >
                    Отмененных заказов нет.
                </Placeholder>
            </div> : null}
        </Panel>
        );
    }
}

export default Orders;