import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import {
	View,
	ScreenSpinner,
	Epic,
	Tabbar,
	TabbarItem
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import './panels/styles.css';

import Store from './panels/Store';
import TestTovar from './panels/TestTovar';
import TestTovar2 from './panels/TestTovar2';
import Filters from './panels/Filters';
import Basket from './panels/Basket';
import Orders from './panels/Orders';

import Icon24StorefrontOutline from '@vkontakte/icons/dist/24/storefront_outline';
import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import Icon28CubeBoxOutline from '@vkontakte/icons/dist/28/cube_box_outline';

const App = () => {
	const [active, setActive] = useState({
        story: 'store',
        panel: 'store'
    });
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(/*<ScreenSpinner size='large' />*/);
	const [showTab, setShowTab] = useState(true);
	
	const updateHistory = (s, p) => {
        window.history.pushState({ story: s, panel: p }, `${s}/${p}`);
    };

	useEffect(() => {
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const onTab = (param) => {
        setShowTab(param);
    };

	const go = (e) => {
        setActive({
            story: active.story,
            panel: e.currentTarget.dataset.to
        });

        updateHistory(active.story, e.currentTarget.dataset.to);
    };

	const onStoryChange = (e) => {
        if (e.currentTarget.dataset.story !== active.story) {
            const id = e.currentTarget.dataset.story;
            setActive({
                story: id,
                panel: id
            });

        } else {
            window.scrollTo({ top: 0, behavior: 'smooth' });
        }
    };

		return (
			<Epic 
				activeStory={active.story}
				tabbar={
					showTab && (
						<Tabbar>
							<TabbarItem
								onClick={onStoryChange}
								selected={active.story === 'store'}
								data-story="store"
								text="Магазин"
							>
								<Icon24StorefrontOutline width={28} height={28} />
							</TabbarItem>

							<TabbarItem
								onClick={onStoryChange}
								selected={active.story === 'basket'}
								data-story="basket"
								text="Корзина"
							>
								<Icon28MarketOutline />
							</TabbarItem>

							<TabbarItem
								onClick={onStoryChange}
								selected={active.story === 'orders'}
								data-story="orders"
								text="Заказы"
							>
								<Icon28CubeBoxOutline />
							</TabbarItem>
						</Tabbar>
					)
				}
			>
				<View
					id="store"
					activePanel={active.panel}
					popout={popout}
				>
					<Store 
						id='store'
						go={go}
						fetchedUser={fetchedUser}
					/>
					<Filters
						id='filters'
						go={go}
					/>
					<TestTovar
						id='testTovar'
						go={go}
					/>
					<TestTovar2
						id='testTovar2'
						go={go}
					/>
				</View>

				<View
					id="basket"
					activePanel={active.panel}
					popout={popout}
				>
					<Basket 
						id='basket'
						go={go}
					/>
					<TestTovar
						id='testTovar'
						go={go}
					/>
					<TestTovar2
						id='testTovar2'
						go={go}
					/>
				</View>

				<View
					id="orders"
					activePanel={active.panel}
				>
					<Orders
						id='orders'
						go={go}
					/>
				</View>
			</Epic>
		);
}

export default App;

