import React from 'react';
import {
    Panel,
    PanelHeader,
    IOS,
    platform,
    PanelHeaderButton,
    Div,
    Button,
    Gallery,
    FormLayout,
    Select
} from '@vkontakte/vkui';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28MarketAddBadgeOutline from '@vkontakte/icons/dist/28/market_add_badge_outline';

import tovar6 from '../img/tovar6.jpg';
import tovar6_2 from '../img/tovar6_2.jpg';

const osname = platform();

class TestTovar1 extends React.Component {
    constructor (props) {
        super(props);

        this.state = {};
    }

render () {
    const {
        id,
        go,
    } = this.props;

return (
    <Panel id={id} separator={false}>
        <PanelHeader
            left={
                <PanelHeaderButton onClick={go} data-to="store">
                    {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
                </PanelHeaderButton>
            }
        >
            Футболка
        </PanelHeader>
        <Gallery
            slideWidth="100%"
            className="open-tovar"
            bullets="light"
        >
        <img type="image" src={tovar6}/>
        <img type="image" src={tovar6_2}/>
        </Gallery>
            <Div>
                <div className="text-open-tovar zapret-videleniya">
                    <h1>Футболка</h1>
                </div>
                <div className="money-open-tovar zapret-videleniya">
                    <h3>150 баллов</h3>
                </div>
                <div className="category-open-tovar zapret-videleniya">
                    ÖMANKÖ for VK Testers
                </div>
                <Div className="description-open-tovar zapret-videleniya">Черная, принт с двух сторон</Div>
            </Div>
            <FormLayout>
                <Select top="Размер" placeholder="Выберите размер">
                    <option value="XS">XS</option>
                    <option value="S">S</option>
                    <option value="M">M</option>
                    <option value="L">L</option>
                    <option value="XL">XL</option>
                </Select>
            </FormLayout>
            <Div><Button size="xl" before={<Icon28MarketAddBadgeOutline />}>Добавить в корзину</Button></Div>
        </Panel>
        );
    }
}

export default TestTovar1;